import React from "react"
import styled from "styled-components"
import mePhoto from "../assets/images/me-1000.webp"
import { StyledButton, Container, StyledHeading } from "../styles/sharedStyles"
import { FiLinkedin, FiGithub } from "react-icons/fi"
// FiCodepen

const Wrapper = styled.div`
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-evenly;

  width: 90%;
  padding: 4em 0;
  margin: 0 auto;
  h2 {
    font-size: 3rem;
    color: ${({ theme }) => theme.colors.main1};
    width: 40%;
    text-align: right;
  }

  .img-container {
    width: 400px;
  }

  article {
    width: 500px;
    color: ${({ theme }) => theme.colors.dark2};

    p {
      margin: 1em 0;
      font-size: 1.2rem;
      line-height: 1.8;
    }

    div {
      display: flex;
      align-items: center;
      width: 50%;
      justify-content: space-between;

      svg {
        color: ${({ theme }) => theme.colors.main1};
        transition: all 0.2s ease;
        &:hover {
          opacity: 0.7;
        }
      }
    }
  }

  @media (max-width: 768px) {
    flex-direction: column;
  }
`

const About = () => (
  <Container className="main" id="about">
    <StyledHeading>About Me</StyledHeading>
    <Wrapper>
      <div className="img-container">
        <img src={mePhoto} alt="Mohamed Afify" />
      </div>
      <article>
        <p>
          I always think of building frontend webpages as making a PowerPoint
          presentation, at the end of the day both are used to effectively tell
          a story or deliver a message except that web pages directly interact
          with the audience and their actions and removes the need for a
          presenter, this idea among many others are fascinating to me and I'm
          in love with the concept and obsessed with the details.
        </p>
        <div>
          <StyledButton
            onClick={() =>
              window.open(
                "https://drive.google.com/file/d/1mrfw5MO94-8uENTEJCUW2mb3FFcfDFGC/view?usp=sharing"
              )
            }
          >
            Resume
          </StyledButton>
          <button
            onClick={() => window.open("https://www.linkedin.com/in/afifym/")}
          >
            <FiLinkedin size={25} />
          </button>
          <button onClick={() => window.open("https://github.com/afifym")}>
            <FiGithub size={25} />
          </button>
        </div>
      </article>
    </Wrapper>
  </Container>
)

export default About
