import React from "react"
import styled from "styled-components"
import vid from "../assets/wavy-blur.mp4"
import { StyledButton } from "../styles/sharedStyles"

const Wrapper = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100vh;
  width: 100%;
  color: white;
  overflow: hidden;
  background-color: #0f1315;

  div {
    position: absolute;
    left: 0;
    top: 0;
    background-color: #111;
    width: 100%;
    height: 100%;

    video {
      width: 100%;
      height: 100%;
      object-fit: cover;
      opacity: 0.7;
    }
  }

  article {
    position: relative;
    z-index: 5;

    h5 {
      font-size: 1.2rem;
    }
    h1 {
      font-size: 3rem;

      span {
        color: white;
      }
    }
    h4 {
      font-size: 2rem;
    }

    button {
      margin-top: 2em;
      font-weight: 600;
    }
  }
`
const HeaderStyledButton = styled(StyledButton)`
  border-radius: 30px;
  font-size: 1rem;
`

const Header = () => {
  return (
    <Wrapper>
      <div>
        <video src={vid} autoPlay loop muted></video>
      </div>
      <article>
        <h5>Hello</h5>
        <h1>
          I'm <span>Mohamed</span>
        </h1>
        <h4>A Frontend Developer/Designer</h4>
        <HeaderStyledButton
          onClick={() =>
            window.open("https://calendly.com/mohamedafify/30min?month=2021-04")
          }
        >
          Book a Meeting
        </HeaderStyledButton>
      </article>
    </Wrapper>
  )
}

export default Header
