import React from "react"
import styled from "styled-components"

const Wrapper = styled.button`
  width: 580px;
  height: 300px;

  margin: 1em;
  padding: 2em 2em;

  text-align: left;
  border-radius: 25px;
  transition: all 0.1s linear;

  position: relative;
  z-index: 1;

  overflow: hidden;

  &:hover {
    /* box-shadow: 0px 0px 73px 10px rgba(0, 0, 0, 0.49); */
    box-shadow: 0px 0px 24px -4px rgba(0, 0, 0, 0.43);

    transform: scale(1.02);
  }

  article {
    display: flex;
    flex-direction: column;
    height: 100%;
    justify-content: space-between;
    position: relative;
    z-index: 3;

    color: ${({ theme }) => theme.colors.dark1};
    img {
      width: 70px;
    }
    h2 {
      font-size: 2rem;
      margin: 0.2em 0;
    }

    p {
      font-size: 1rem;
      width: 95%;
    }
  }

  button {
    position: relative;
    z-index: 3;
    margin-top: 2em;
    padding: 0.8em 1.6em;
    border: 1px solid ${({ theme }) => theme.colors.dark1};
    color: ${({ theme }) => theme.colors.dark1};
    border-radius: 25px;

    font-weight: 500;

    transition: all 0.1s linear;

    &:hover {
      background-color: ${({ theme }) => theme.colors.dark1};
      color: ${({ theme }) => theme.colors.light1};
    }
  }

  &.dark {
    article {
      color: ${({ theme }) => theme.colors.light1};
    }

    button {
      border-color: ${({ theme }) => theme.colors.light1};
      color: ${({ theme }) => theme.colors.light1};

      &:hover {
        background-color: ${({ theme }) => theme.colors.light1};
        color: ${({ theme }) => theme.colors.dark1};
      }
    }
  }

  .bg {
    pointer-events: none;
    position: absolute;
    width: 100%;
    height: 100%;
    background-repeat: no-repeat;
    background-size: cover;
    top: 0;
    left: 0;
    z-index: 2;
    filter: blur(5px);
    opacity: 0.5;
  }
`

const ProjectItem = ({ data }) => {
  const handleWebsite = e => {
    e.stopPropagation()
    window.open(data.url)
  }

  return (
    <>
      {data.name && (
        <Wrapper
          itemTheme={data.theme}
          className={`${data.theme === "light" ? "light" : "dark"}`}
          onClick={handleWebsite}
        >
          <div
            className="bg"
            style={{
              backgroundImage: `url(${data.image})`,
            }}
          ></div>
          <article>
            <img src={data.icon} alt={data.name} />
            <h2>{data.name}</h2>
            <p>{data.description}</p>
            <div className="btn-group">
              <button onClick={handleWebsite}>View Website</button>
            </div>
          </article>
        </Wrapper>
      )}
    </>
  )
}

export default ProjectItem
